import React from 'react';
import styled from 'styled-components';
import imgPlantacle from '../../images/iphone_mockup.png';
import imgILE from '../../images/macbook_mockup--ILE.png';
import imgBlownAway from '../../images/portfoliofigureblownaway.png';
import imgMusicTerra from '../../images/portfoliomusicterra.png';
import imgPhaseRunner from '../../images/portfoliophaserunner.png';
import imgStrakzat from '../../images/portfolio-strakzat.png';

const MockupImage = styled.div`
    background-image: url(${props => props.title === 'Plantacle' ? imgPlantacle : props.title === 'Blown Away' ? imgBlownAway : props.title === 'Music Terra' ? imgMusicTerra : props.title === 'Phase Runner' ? imgPhaseRunner : props.title === 'Strakzat' ? imgStrakzat : null });
    background-size: 278px 287px;
    background-repeat: no-repeat;
    width: 100%;
    height: 287px;
    margin: 0 auto;
    background-position: center;

    @media (max-width: 500px) {
        background-size: 500px 289px;
        background-size: ${(props) => props.title === 'Phase Runner' ? '600px 389px' : props.title === 'Plantacle' ? '350px 300px' : props.title === 'Strakzat' ? '350px 250px' : '500px 289px'};
        width: 400px;
        height: 389px;
    }

    @media (min-width: 500px) {
        background-size: 500px 289px;
        background-size: ${(props) => props.title === 'Phase Runner' ? '600px 389px' : props.title === 'Plantacle' ? '350px 300px' : props.title === 'Strakzat' ? '400px 250px' : '500px 289px'};
        width: 400px;
        height: 389px;
    }

    @media (min-width: 768px) {
        background-size: ${(props) => props.title === 'Phase Runner' ? '800px 570px' : props.title === 'Plantacle' ? '550px 500px' : props.title === 'Strakzat' ? '500px 350px' : '700px 469px'};
        width: 498px;
        height: 487px;
    }

    @media (min-width: 1024px) {
        background-size: ${(props) => props.title === 'Phase Runner' ? '850px 620px' : props.title === 'Plantacle' ? '550px 500px' : props.title === 'Strakzat' ? '560px 410px' : '700px 469px'};
        width: 598px;
        height: 487px;

        // Center
        position: absolute;
        top: 50%;
        left: 62%;
        transform: translate(-50%, -50%);
    }

    @media (min-width: 1200px) {
        // background-size: 1000px 609px;
        background-size: ${(props) => props.title === 'Phase Runner' ? '1100px 759px' : props.title === 'Plantacle' ? '700px 650px' : props.title === 'Strakzat' ? '730px 480px' : '1000px 609px'};
        width: 708px;
        height: 697px;
    }
`;

const Mockup = (props) => {
    return(
        <div className="mockup-wrapper">
            {/* <MockupImage title={props.title} widthPlantacle={props.widthPlantacle} widthBanzai={props.widthBanzai} heightPlantacle={props.heightPlantacle} heightBanzai={props.heightBanzai}> */}
            <MockupImage title={props.title}>
            </MockupImage>
        </div>
    )
};

export default Mockup;
